

/* Footer Styles */
footer {
    background-color: rgb(22, 65, 87);
    height: 300px;
    padding: 20px 0;
}

/* Contact Section */
.contact-section {
    display: flex;
    justify-content: space-evenly;
    padding-top: 30px;
    width: 100%;
    height: 100px;
}

.contact-box {
    display: flex;
    padding-left: 35px;
    align-items: center;
    color: white;
    background-color: rgb(42, 99, 128);
    width: 20rem;
    border-radius: 15px;
}

.content-box {
    padding-left: 20px;
    font-size: 15px;
}

.footer-icon {
    color: white;
    font-size: 30px;
    padding: 10px;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

/* Company Section */
.company-section {
    display: flex;
    color: white;
    padding-left: 80px;
    margin-top: 20px;
    gap: 130px;
    font-size: 13px;
    line-height: 1.5;
}

.company-footer-logo {
    width: 22%;
    height: 90px;
}

.company-footer-logo img {
    width: 100%;
    height: 90px;
}

.company-tagline p {
    margin: 0;
    width: 24rem;
}

/* Footer Line */
.footer-line {
    display: flex;
    color: white;
    margin-top: 20px;
    font-size: 13px;
    padding-left: 80px;
}

.footer-line i {
    margin-right: 10px;
}


@media (max-width: 480px) {
    
    .contact-section{
        flex-wrap: wrap;
    }
    .company-section{
        height: max-content;
        flex-wrap: wrap;
        margin: 10px 0px;
        
        
    }
    .company-footer-logo {
        width: max-content;
        height: 90px;
    }
    .footer-line{
        padding-left: 10px;
    }
}

@media (max-width: 768px) {
    footer{
        height: max-content;
    }
    .contact-section{
        flex-wrap: wrap;
        width: fit-content;
        height: max-content;
    }

    .contact-box{
        margin: 10px 0px;
    }
    .company-section{
        gap: 0;
        height: max-content;
        flex-wrap: wrap;
        padding-left: 20px;
        margin-top: 10px;
        justify-content: space-evenly;
    }
    

}