/* Latest News Section Styles */
.latest-news-section {
    display: flex;
    padding-left: 30px;
    padding-right: 30px;
    width: 100%;
    height: 35rem;
    justify-content: space-evenly;
}

.news-content-box1, .news-content-box2 {
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    
}

.big-content {
    width: 100%;
    height: 310px;
    border-radius: 10px 10px;
}

.small-content {
    width: 100%;
    height: 150px;
    border-radius: 10px 10px;
}

.image-medicine {
    background-image: url(img7.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.image-hand {
    background-image: url(img8.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.tag {
    color: white;
    margin-top: 20px;
    background-color: rgb(22, 65, 87);
    width: 80px;
    padding: 5px 20px;
    font-size: 12px;
    border-radius: 0px 13px 13px 0px;
    transform: translate(-12%, -12%);
}

.news-headline {
    color: white;
    width: 75%;
    font-size: 14px;
    margin-left: 25px;
    margin-top: 15px;
    font-weight: 520;
}

@media (max-width: 768px) {
    .latest-news-section {
        flex-wrap: wrap;
        height: max-content;
    }
    
    .news-content-box1, .news-content-box2 {
        gap:10px;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}


@media (max-width: 480px) {
    .latest-news-section {
        flex-wrap: wrap;
    }
    
    .news-content-box1, .news-content-box2 {
        width: 200px;
        gap:10px;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}